<template>
  <div>
    <div>
      <b-form @submit.stop.prevent>
        <label for="text-password">Insert your search, and the filters you would like</label>
        <b-form-input v-model="search_string" id="querystring" ></b-form-input>
        <b-form-text id="password-help-block">
          In case of adding filtering the search will only consist of spooncular results!
        </b-form-text>
      </b-form>
    </div>
    <div id="dropdowns">
    <multiselect
      v-model="selection.selected_cuisines"
      :options="options.cuisines"
      placeholder="select cuisines to exclude"
      multiple
      size="lg">      
    </multiselect>
    <multiselect
      v-model="selection.selected_diet"
      :options="options.diets"
      placeholder="select diet"
      >      
    </multiselect>
    <multiselect
      v-model="selection.selected_intolerances"
      :options="options.intolerance"
      placeholder="select intolerances"
      multiple>      
    </multiselect>
    </div>    
    <div>    
      <b-button type="submit" variant="success" >Search</b-button>
    </div>
  </div>
</template>

<script>
  import Multiselect from 'vue-multiselect'
  export default {
    components: { Multiselect },
    data () {
      return {      
        options:{
          cuisines:[],
          diets: [],
          intolerance : [],
        },        
        selection:{
            selected_diet: '',
            selected_cuisines:[],
            selected_intolerances:[]
        }   ,
        search_string: ''     
      }
    },
    mounted () {
        this.getOptions();
    },
    methods:
    {
      async getOptions(){
        try {
          const response = await this.axios.get(
            this.$root.store.server_domain + '/Recipes/Options/RecipeSearch', 
            {withCredentials: true}
          );
          const cuisines = response.data.cousines;
          const diets = response.data.diets;
          const intolerance = response.data.intolerances;
          this.options.cuisines = cuisines;
          this.options.diets = diets;
          this.options.intolerance = intolerance;
          // console.log(this.recipes);
        } catch (error) {
          console.log(error);
        }
      },
      onSubmit(event){
        event.preventDefault()
        window.alert(selection)
        alert(search_string)
        console.log("ben")
      }
    }    
  }
</script>

<style src="vue-multiselect/dist/vue-multiselect.min.css">

</style>